import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';
import { Layout, Spin } from 'antd';
import * as comMap from '@components';
import { firstToUpper, filterObj } from '@components/utils/fun'
import Apis from '@/apis'
const { Content } = Layout;

@inject(({ appStore }) => ({
  ...appStore
}))
@observer
class PageView extends Component {
  state = {
    title: '',
    name: '',
    tbName: '',
    pageLayout: '',
    moduleName: '',
    components: [],
    viewData: {},
    timestamp: 0
  }

  componentDidMount() {
    this.fetchData()
  }

  fetchData = async () => {
    const { view, } = this.props
    if (view) {
      let res = await Apis.viewList({ name: view, })
      if (Array.isArray(res) && res.length) {
        let { title, name, tbName, components, pageLayout, moduleName } = res[0]
        this.setState({
          title,
          name,
          tbName,
          pageLayout,
          components,
          moduleName,
          viewData: res[0],
          timestamp: +new Date()
        })
      }
    }
  }

  render() {
    let { components, tbName, pageLayout, moduleName, viewData, timestamp } = this.state
    return (
      <Content>
        {(Array.isArray(components) && components.length) ?
          <>
            {
              components.map((item, index) => {
                let { __component, __v, id, _id, ...itemProps } = item
                let Com = comMap[firstToUpper(__component.replace('view.', ''))]
                return Com ? <Com
                  {...filterObj(this.props, val => val !== undefined || val !== null)}
                  tbName={tbName}
                  pageLayout={pageLayout}
                  moduleName={moduleName}
                  viewData={viewData}
                  comName={__component}
                  onUpdate={this.fetchData}
                  timestamp={timestamp}
                  {...filterObj(itemProps, val => val !== undefined || val !== null)}
                  key={`com_${index}`}
                /> : null
              })
            }
          </>
          : <div className="app-spin"><Spin /></div>
        }
      </Content>
    )
  }
};

export default PageView;
